.contenedor-servicios{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    margin: 2rem 0;

    @include tablet{
        @include grid(2, 2rem)
    }

    .servicios{
        background-color: $blanco;
        padding: $separacion / 2;
        border-radius: 2rem;
        display: flex;
        flex-direction: column;
        cursor: pointer;

        &.seleccionado{
            background-color: $azul;
    
    
            p,h4{
                color: $blanco;
            }
        }
    }

    .titulo__servicios{
        font-size: 2.2rem;
        font-weight: $regular;
        margin: 2rem 0;

        @include tablet{
            font-size: 2rem;
        }
    
    }

    .precio__servicios{
        color: $azul;
        text-align: start;
        font-size: 3rem;
        font-weight: $bold;

        @include tablet{
            font-size: 3rem;
        }
    }
}

