html{
    font-size: 62.5%;
    box-sizing: border-box;
}

body{
    font-family: $fuentePrincipal;
    font-size: 1.6rem;
}

p{
    font-size: 2rem;
}

a{
    text-decoration: none;
}

li{
    list-style: none;
}

img{
    max-width: 100%;
}

h1{
    font-size: 4rem;
    color: $azul;
    text-align: center;
}

h2{
    font-size: 3.6rem;
    color: $azul;
    text-align: center;
}

h3{
    font-size: 3rem;
    color: $azul;
    text-align: center;
}

.contenedor{
    max-width: 120rem;
    width: 95%;
    margin: 0 auto;
}
