body{
    background-color: $negro;
}

.contenedor-estetica{
    display: flex;
    flex-direction: column;
    @include tablet{
        height: 100vh;
        @include grid(2, $separacion)
    }


        .img{
            @include tablet{
                height: auto;
            }
            background-image: url(../img/1.webp);
            background-size: cover;
            background-position: center;
            height: 30rem;
        }
}

.app{
    padding-top: 2.5rem;
}

.header{

    h1{
        margin-bottom: 2rem;
    }
}

.navegacion{
    display: flex;
    justify-content: space-between;
    gap: 1rem;
    font-size: 1.5rem;

    @include tablet{
        text-align:center;

        @include grid(3, 3rem)
    }

    
    button{
        border: none;
        padding: 1rem;
        background-color: $blanco;
        color: $negro;
        font-weight: $bold;
        cursor: pointer;
        text-transform: uppercase;

        &.activo{
            background-color: $azul;
            color: $blanco;
        }
    }

}





.seccion{
    display: none;
    h2{
        margin: 3rem 0 2rem 0;
    }

    .subtitulo{
        text-align: center;
        color: $blanco;
        font-weight: $regular;
    }

    button{
        border: none;
        padding: 1rem;
        background-color: $azul;
        color: $blanco;
        font-weight: $bold;
        cursor: pointer;
        text-transform: uppercase;
        text-align: center;
        align-self: center;
    }
}

.mostrar-seccion{
    display: block!important;
}    