.formulario{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 2rem;

    .campo{
        display: flex;
        margin-bottom: 1rem;
        align-items: center;
        

        label{
            flex:0 0 10rem;
        }

        input{
            padding: 1rem;
            border: none;
            border-radius: 1rem;
            flex: 1;
        }
    }
}

.label{
    color: $blanco;
}