.nombre-error{
    text-align: center;
    padding: $separacion / 2;
    width: 100%;
    display: block;
    background-color: #e50914;
}

.correcto{
    text-align: center;
    padding: $separacion / 2;
    width: 100%;
    display: block;
    background-color: rgb(20, 245, 84);
}

.datos{
    padding: 1.5rem 0;
    text-align: center;
    color: $blanco;
    font-weight: $regular;
}

.resumen__datos{
    color: $azul;
    font-weight: $bold;
    margin: 2rem 0;
    
        span{
            color: $blanco;
            font-weight: $regular;
        }
}

.contenedor-servicio{
    border-bottom: 2px solid #43453c;
    color: $blanco;
    padding: $separacion / 2 ;

    .precio-js{
        color: $azul;
        margin-top: 1rem;
        font-weight: $black;
    }
}

.pagar{
    text-align: right;
    color: $azul;
    font-weight: $bold;
    padding: 1.5rem 1rem;

        span{
            color: $blanco;
            font-weight: $regular;
        }
}