.paginacion{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: $separacion 0 ;
    button{

        @include tablet{
            padding: 1rem  3rem;
            font-size: 2rem;
        }
        background-color: $azul;
        border: none;
        padding: .5rem 1.5rem;
        color: $blanco;
        border-radius: .5rem;
        text-transform: uppercase;
        font-size: 1.5rem;
        text-align: center;
        font-weight: $bold;
        cursor: pointer;

        &.ocultar{
            visibility: hidden;
        }
    }
}